import * as React from "react";
import {IExtensionUserInfo} from "@ceros/markup-common/frontend/extension/extension-user-info";
import {IExtensionDeprecationWarning} from "@ceros/markup-common/frontend/extension/extension-deprecation-warning";
import {UserInfoStorage} from "../../shared/storage/user-info-storage";
import {DeprecationStorage} from "../../shared/storage/deprecation-storage";

import {Header} from "./header";
import {CreateProject} from "./create-project";
import {RecentProjects} from "./recent-projects";
import {DeprecationWarning} from "./deprecation-warning";
import "./main.scss";
import {ClearCookies} from "./clear-cookies";
import {APP_ORIGIN} from "../../shared/app-origin";
import {isUUID} from "@ceros/markup-common/shared/util/string-utils";
import {getActiveTab, sendTabMessage} from "../../shared/chrome-api-promises";
// eslint-disable-next-line max-len
import {
    isIframeUsesProxyMessage,
    MessageFromExtensionBackgroundScriptType
} from "@ceros/markup-common/frontend/extension/messages/extension-background-script-message";
interface IState {
    userInfo: IExtensionUserInfo;
    deprecationWarning: IExtensionDeprecationWarning;
    projectId?: string;
    /**
     * Loading through extension can fail, in that case we fallback to proxy.
     * We may render different popup UI depending on whatever we use extension or proxy (for now just the 'Clear cookies' button),
     * so we need to check that.
     */
    projectUsesProxy: boolean;
}

export class Main extends React.Component<{}, IState> {
    public constructor(props: {}) {
        super(props);

        this.state = {
            userInfo: null,
            deprecationWarning: null,
            // we're assuming that extension failed to load project until we know it didn't
            projectUsesProxy: true
        };
        this.checkIfProjectPage = this.checkIfProjectPage.bind(this);
        this.handleMessage = this.handleMessage.bind(this);
    }

    public async componentWillMount(): Promise<void> {
        const userInfo: IExtensionUserInfo = await UserInfoStorage.get();
        const deprecationWarning: IExtensionDeprecationWarning = await DeprecationStorage.get();
        this.setState({userInfo, deprecationWarning});
        await this.checkIfProjectPage();
        chrome.runtime.onMessage.addListener(this.handleMessage);
        await this.checkIfProjectLoadedProperlyUsingExtension();
    }

    public render(): JSX.Element {
        return (
            <ul>
                <Header userInfo={this.state.userInfo} deprecationWarning={this.state.deprecationWarning} />
                {this.renderContent()}
            </ul>
        );
    }

    public renderContent(): JSX.Element {
        if (!this.state.deprecationWarning) {
            return (
                <>
                    <CreateProject userInfo={this.state.userInfo} />
                    {this.state.projectId && !this.state.projectUsesProxy && (
                        <ClearCookies userInfo={this.state.userInfo} projectId={this.state.projectId} />
                    )}
                    <RecentProjects userInfo={this.state.userInfo} />
                </>
            );
        } else {
            return <DeprecationWarning deprecationWarning={this.state.deprecationWarning} />;
        }
    }

    private async checkIfProjectPage(): Promise<void> {
        const tab: chrome.tabs.Tab = await getActiveTab();
        if (tab.url && tab.url.startsWith(APP_ORIGIN)) {
            const projectId: string | undefined = tab.url.split(/\/markup\//g)[1];
            if (projectId && isUUID(projectId)) {
                this.setState({projectId});
            }
        }
    }

    private async checkIfProjectLoadedProperlyUsingExtension(): Promise<void> {
        const tab: chrome.tabs.Tab = await getActiveTab();
        // We only want to check storage that holds failed project frames when active tab is loaded.
        // If it's not loaded the 'iframe-uses-proxy' message handler will take care of that.
        if (tab.id && tab.status === "complete") {
            // proxy extension message handlers responds to this message with 'iframe-uses-proxy' message
            await sendTabMessage(tab.id!, {type: MessageFromExtensionBackgroundScriptType.IS_ACTIVE_IFRAME_LOADED_THROUGH_PROXY});
        }
    }

    private async handleMessage(message: unknown, sender: chrome.runtime.MessageSender): Promise<void> {
        const tab: chrome.tabs.Tab = await getActiveTab();

        if (isIframeUsesProxyMessage(message) && tab.id && tab.id === sender.tab?.id) {
            this.setState({
                projectUsesProxy: message.isUsingProxy
            });
        }
    }
}
