import React from "react";
import {IExtensionUserInfo} from "@ceros/markup-common/frontend/extension/extension-user-info";
import {IProjectOpen} from "@ceros/markup-common/frontend/project-open";
import {ProjectOpenStorage} from "../../shared/storage/project-open-storage";
import {RecentProjectListItem} from "./recent-project-list-item";
import folderIcon from "../assets/folder-icon.svg.inline";
import "./recent-projects.scss";

interface IProps {
    userInfo: IExtensionUserInfo;
}

interface IState {
    isListOpen: boolean;
    projectOpens: IProjectOpen[];
}

export class RecentProjects extends React.Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);

        this.state = {
            isListOpen: false,
            projectOpens: []
        };
    }

    public async componentWillMount(): Promise<void> {
        const projectOpens: IProjectOpen[] = await ProjectOpenStorage.get();
        this.setState({projectOpens});
    }

    public render(): JSX.Element {
        const className: string = ["recent-projects", ...(this.state.isListOpen ? ["open"] : []), ...(!this.props.userInfo ? ["disabled"] : [])].join(
            " "
        );

        return (
            <>
                <li className={className} onClick={this.onClick.bind(this)}>
                    <div className="image-column">
                        <span dangerouslySetInnerHTML={{__html: folderIcon}} />
                    </div>
                    <div className="content-column">Open a MarkUp</div>
                </li>
                {this.renderList()}
            </>
        );
    }

    private renderList(): JSX.Element {
        if (this.state.isListOpen) {
            if (this.state.projectOpens.length > 0) {
                return (
                    <li className="recent-project-list">
                        <ul>{this.renderListItems(this.state.projectOpens)}</ul>
                    </li>
                );
            } else {
                return (
                    <li className="no-recent-projects">
                        <div className="image-column"></div>
                        <div className="content-column">You have no recent MarkUps</div>
                    </li>
                );
            }
        } else {
            return <></>;
        }
    }

    private renderListItems(models: IProjectOpen[]): JSX.Element[] {
        return models.map((model: IProjectOpen): JSX.Element => <RecentProjectListItem key={model.projectId} projectOpen={model} />);
    }

    private onClick(e: React.MouseEvent): void {
        if (this.props.userInfo) {
            this.setState({isListOpen: !this.state.isListOpen});
        }
    }
}
