import * as React from "react";
import moment from "moment";
import {IProjectOpen} from "@ceros/markup-common/frontend/project-open";

interface IProps {
    projectOpen: IProjectOpen;
}

export class RecentProjectListItem extends React.Component<IProps> {
    public render(): JSX.Element {
        return (
            <li className="recent-project" onClick={this.onClick.bind(this)}>
                <div className="image-column">
                    <img src={this.props.projectOpen.thumbnailUrl} />
                </div>
                <div className="content-column">
                    <div className="text-column">{this.props.projectOpen.name}</div>
                    <div className="note-column">{moment(this.props.projectOpen.openedAt).fromNow()}</div>
                </div>
            </li>
        );
    }

    private onClick(e: React.MouseEvent): void {
        window.open(this.props.projectOpen.url);
    }
}
