import * as React from "react";
import {IExtensionDeprecationWarning} from "@ceros/markup-common/frontend/extension/extension-deprecation-warning";
import {DeprecationStorage} from "../../shared/storage/deprecation-storage";

import {Header} from "./header";
import {CreateProject} from "./create-project";
import {RecentProjects} from "./recent-projects";
import "./deprecation-warning.scss";

interface IProps {
    deprecationWarning: Exclude<IExtensionDeprecationWarning, null>;
}

export class DeprecationWarning extends React.Component<IProps> {
    public constructor(props: IProps) {
        super(props);
    }

    public render(): JSX.Element {
        return (
            <li className="deprecation-warning">
                <p>This version of the MarkUp extension is no longer supported.</p>
                {this.renderUpgradeLink()}
            </li>
        );
    }

    public renderUpgradeLink(): JSX.Element {
        if (this.props.deprecationWarning.upgradeUrl) {
            return (
                <p>
                    <a className="upgrade-link" href={this.props.deprecationWarning.upgradeUrl} target="_blank">
                        Please upgrade to the most recent version
                    </a>
                    <span>.</span>
                </p>
            );
        } else {
            return <></>;
        }
    }
}
