import * as React from "react";
import {fireAndForget} from "@ceros/markup-common/shared/util/promise-utils";
import {Logger} from "@ceros/markup-common/frontend/logger";
import {IExtensionUserInfo} from "@ceros/markup-common/frontend/extension/extension-user-info";
import {
    MessageToExtensionBackgroundScriptType,
    isProjectCreationError
} from "@ceros/markup-common/frontend/extension/messages/extension-background-script-message";
import {APP_ORIGIN} from "../../shared/app-origin";
import {getActiveTab, sendMessage} from "../../shared/chrome-api-promises";
import "./create-project.scss";

enum LoadingState {
    NONE,
    LOADING,
    ERROR
}

interface IProps {
    userInfo: IExtensionUserInfo;
}

interface IState {
    urlUnimportableMessage: string | undefined;
    loadingState: LoadingState;
}

export class CreateProject extends React.Component<IProps, IState> {
    private isTabImportable: boolean;

    constructor(props: IProps) {
        super(props);

        this.handleMessage = this.handleMessage.bind(this);

        this.state = {
            urlUnimportableMessage: undefined,
            loadingState: LoadingState.NONE
        };

        this.isTabImportable = false;
    }

    public async componentWillMount(): Promise<void> {
        chrome.runtime.onMessage.addListener(this.handleMessage);

        this.isTabImportable = await sendMessage({type: MessageToExtensionBackgroundScriptType.IS_TAB_IMPORTABLE});
        await this.updateUrlUnimportableMessage();
    }

    public componentWillUnmount(): void {
        chrome.runtime.onMessage.removeListener(this.handleMessage);
    }

    public componentDidUpdate(): Promise<void> {
        // user info is set asynchronously so we need to refresh the error message on update
        return this.updateUrlUnimportableMessage();
    }

    public render(): JSX.Element {
        return (
            <li className={`create-project ${this.isDisabled() ? "disabled" : ""}`} onClick={this.onClick.bind(this)}>
                <div className="image-column">{this.renderIcon()}</div>
                <div className="content-column">
                    <span>{this.state.urlUnimportableMessage || "Create MarkUp"}</span>
                    {this.state.loadingState === LoadingState.ERROR ? <span className="failed">Failed</span> : ""}
                </div>
            </li>
        );
    }

    public async updateUrlUnimportableMessage(): Promise<void> {
        const tab: chrome.tabs.Tab = await getActiveTab();

        let message: string | undefined;

        if (this.props.userInfo) {
            if (!tab.url || !(tab.url.startsWith("http://") || tab.url.startsWith("https://")) || !this.isTabImportable) {
                message = "Open a webpage to create MarkUp";
            } else if (tab.url.startsWith(APP_ORIGIN)) {
                message = "Cannot import MarkUp into itself";
            }
        }

        this.setState({urlUnimportableMessage: message});
    }

    private handleMessage(message: unknown): void {
        if (isProjectCreationError(message) && this.state.loadingState === LoadingState.LOADING) {
            this.setState({loadingState: LoadingState.ERROR});
        }
    }

    private renderIcon(): JSX.Element {
        switch (this.state.loadingState) {
            case LoadingState.LOADING:
                return (
                    <div className="loading-spinner">
                        <div></div>
                        <div></div>
                        <div></div>
                        <div></div>
                    </div>
                );

            case LoadingState.ERROR:
                return <span className="error">&times;</span>;

            default:
                return <span className="plus">+</span>;
        }
    }

    private isDisabled(): boolean {
        return !this.props.userInfo || !!this.state.urlUnimportableMessage;
    }

    private onClick(e: React.MouseEvent): void {
        if (this.isDisabled()) {
            return;
        }
        fireAndForget(
            (async (): Promise<void> => {
                const messageAccepted: boolean = await sendMessage({type: MessageToExtensionBackgroundScriptType.POPUP_CREATE_PROJECT});
                if (!messageAccepted) {
                    this.setState({loadingState: LoadingState.ERROR});
                }
            })(),
            Logger
        );
        this.setState({loadingState: LoadingState.LOADING});

        setTimeout((): void => {
            this.setState({loadingState: LoadingState.ERROR});
        }, 15000);

        // there is no check for success because on success a new tab is opened by the background script closing the popup automatically
    }
}
