import * as React from "react";
import {IExtensionUserInfo} from "@ceros/markup-common/frontend/extension/extension-user-info";
import {IExtensionDeprecationWarning} from "@ceros/markup-common/frontend/extension/extension-deprecation-warning";
import {APP_ORIGIN} from "../../shared/app-origin";
import "./header.scss";

interface IProps {
    userInfo: IExtensionUserInfo;
    deprecationWarning: IExtensionDeprecationWarning;
}

interface IState {
    signupHover: boolean; // styling hack: see stylesheet for details
}

export class Header extends React.Component<IProps, IState> {
    public constructor(props: IProps) {
        super(props);

        this.state = {
            signupHover: false
        };
    }

    public render(): JSX.Element {
        return (
            <li className={`header ${this.state.signupHover ? "signup-hover" : ""}`} onClick={this.onClick.bind(this)}>
                <div className="image-column">
                    <img src="assets/logo-48.png" />
                </div>
                <div className="content-column">
                    <div className="text-column">MarkUp.io</div>
                    {this.renderUserLinks()}
                </div>
            </li>
        );
    }

    private renderUserLinks(): JSX.Element {
        if (!this.props.deprecationWarning) {
            if (this.props.userInfo) {
                return <div className="note-column">{this.props.userInfo.name}</div>;
            } else {
                return (
                    <div className="note-column">
                        <span className="login">Login</span>
                        <a
                            className="signup"
                            href={`${APP_ORIGIN}/signup`}
                            target="_blank"
                            onMouseEnter={this.onSignupMouseEnter.bind(this)}
                            onMouseLeave={this.onSignupMouseLeave.bind(this)}
                        >
                            Sign Up
                        </a>
                    </div>
                );
            }
        } else {
            return <></>;
        }
    }

    private onClick(e: React.MouseEvent): void {
        if ((e.target as any).tagName && (e.target as any).className !== "signup") {
            window.open(`${APP_ORIGIN}/`);
        }
    }

    private onSignupMouseEnter(e: React.MouseEvent): void {
        this.setState({signupHover: true});
    }

    private onSignupMouseLeave(e: React.MouseEvent): void {
        this.setState({signupHover: false});
    }
}
