order-color;\n    margin-top: -$size - 1;\n  }\n  &:after {\n    @include arrow-piece-horizontal($size, $left);\n    top: 1px;\n    border-top: 0;\n    border-bottom-color: $bg-color;\n    margin-top: -$size;\n  }\n}\n\n@mixin arrow-bottom($left: 50%, $size: 10px, $border-color: $light-grey-2, $bg-color: $white) {\n  &:before {\n    @include arrow-piece-horizontal($size + 3, $left);\n    bottom: -1px;\n    border-bottom: 0;\n    border-top-color: $border-color;\n    margin-left: -$size - 2;\n    margin-bottom: -$size - 1;\n  }\n  &:after {\n    @include arrow-piece-horizontal($size, $left);\n    bottom: 1px;\n    border-bottom: 0;\n    border-top-color: $bg-color;\n    margin-bottom: -$size;\n  }\n}\n\n@mixin arrow-right($size, $top, $bg-color) {\n  &:after {\n    @include arrow-piece-horizontal($size, 100%);\n    top: $top;\n    border-left-color: $bg-color;\n    border-right: 0;\n    margin-top: -$size;\n  }\n}\n\n@mixin arrow-right-bordered($top: 0px, $size: 10px, $border-color: $light-grey-2, $bg-color: $white) {\n  &:before {\n    @include arrow-piece-horizontal($size + 1, 100%);\n    top: $top;\n    border-left-color: $border-color;\n    border-right: 0;\n    margin-left: unset;\n    margin-top: -$size;\n    margin-right: -$size - 2;\n  }\n  &:after {\n    @include arrow-piece-horizontal($size, -2px);\n    top: calc($top + 1px);\n    border-left-color: $bg-color;\n    border-right: 0;\n    margin-left: unset;\n    margin-top: -$size;\n    margin-right: -$size;\n  }\n}\n\n@mixin arrow-left($size, $top, $bg-color) {\n  &:after {\n    @include arrow-piece-horizontal($size, 0);\n    top: $top;\n    border-right-color: $bg-color;\n    border-left: 0;\n    margin-top: -$size;\n  }\n}\n\n@mixin arrow-left-bordered($top: 0px, $size: 10px, $border-color: $light-grey-2, $bg-color: $white) {\n  &:before {\n    @include arrow-piece-horizontal($size + 1, 100%);\n    top: $top;\n    border-right-color: $border-color;\n    border-left: 0;\n    margin-top: -$size;\n    margin-left: -$size - 1;\n  }\n\n  &:after {\n    @include arrow-piece-horizontal($size, 1px);\n    top: calc($top + 1px);\n    border-right-color: $bg-color;\n    border-left: 0;\n    margin-top: -$size;\n    margin-right: -$size;\n  }\n}\n\n// this is taken from https://loading.io/spinner\n@mixin html-spinner($side-length: 80px, $color: #000) {\n  position: relative;\n  div {\n    box-sizing: border-box;\n    display: block;\n    position: absolute;\n    width: $side-length;\n    height: $side-length;\n    border: math.div($side-length, 16) solid $color;\n    border-radius: 50%;\n    animation: html-spinner 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;\n    border-color: $color transparent transparent transparent;\n\n    &:nth-child(1) {\n      animation-delay: -0.45s;\n    }\n    &:nth-child(2) {\n      animation-delay: -0.3s;\n    }\n    &:nth-child(3) {\n      animation-delay: -0.15s;\n    }\n  }\n}\n@keyframes html-spinner {\n  0% {\n    transform: rotate(0deg);\n  }\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n@mixin html-spinner-overlay($side-length: 80px, $opacity: 1, $color: #000) {\n  position: absolute;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  opacity: $opacity;\n  background: $white;\n  .html-spinner {\n    @include html-spinner($side-length, $color);\n    height: $side-length;\n    width: $side-length;\n  }\n}\n\n@mixin blink-animation($prefix, $first-color, $second-color, $time, $iteration-count){\n  animation: #{$prefix}-blink-animation $time linear $iteration-count;\n  @keyframes #{$prefix}-blink-animation {\n    0% {\n      border-color: $first-color;\n    }\n    100% {\n      border-color: $second-color;\n    }\n  }\n}\n\n@mixin pulsation-animation($prefix, $gradient-size, $time: 1.2s, $color1: #f0f0f0, $color2: #f8f8f8, $direction: \"x\") {\n  $wave-size: math.div($gradient-size, math.div($gradient-size, 100%) - 1);\n  background: linear-gradient(-90deg, $color1 0%, $color2 50%, $color1 100%);\n  background-size: $gradient-size $gradient-size;\n  animation: #{$prefix}-pulsation $time ease-in-out infinite;\n  @keyframes #{$prefix}-pulsation {\n    0% {\n      background-position-#{$direction}: 0%;\n    }\n    100% {\n      background-position-#{$direction}: -$wave-size;\n    }\n  }\n}\n\n@mixin round-x($size, $fontSize: 1rem) {\n  @include circle($size);\n  font-size: $fontSize;\n}\n\n@mixin circle($size) {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  height: $size;\n  width: $size;\n  border-radius: math.div($size, 2);\n}\n\n@function pxToNumber($px) {\n  @return math.div($px, 1px);\n}\n\n@mixin word-break() {\n  overflow-wrap: break-word;\n  word-wrap: break-word;\n\n  -ms-word-break: break-all;\n  /* This works in some browsers but doesn't work in webkit */\n  word-break: break-all;\n  /* Instead use this non-standard one for webkit. Browsers that don't support it will fall back to break-all */\n  word-break: break-word;\n}\n\n@mixin no-scrollbar() {\n  scrollbar-width: none;                  // firefox\n  &::-webkit-scrollbar { display: none; } // webkit\n}\n\n@mixin card($border-radius) {\n  position: relative;\n  border-radius: $border-radius;\n  background: $white;\n  box-shadow: 10px 10px 20px $light-grey;\n  border: $border-style;\n}\n\n@mixin card-hover($selector-to-blur-on-hover) {\n  &:hover, &:focus {\n    border: 1px solid $blue;\n    box-shadow: 10px 10px 20px $light-grey-4;\n    #{$selector-to-blur-on-hover} {\n      @include transform(translate(0, 0));\n      @include filter(blur(10px));\n      @include transform(scale(1.15));\n    }\n  }\n}\n\n$default-grid-map: (\"500px\": 50%, \"900px\": 33.33%, \"1300px\": 25%, \"1700px\": 20%, \"2100px\": 16.66%);\n\n@mixin card-list-grid($label, $grid-gap, $grid-map: $default-grid-map) {\n  .#{$label}-list {\n    display: flex;\n    flex-wrap: wrap;\n    padding: 10px 0px;\n    margin-right: -$grid-gap;\n  \n    .#{$label}-list-grid-item {\n      width: 100%;\n      margin: 0;\n      flex-direction: column;\n      padding: 0 $grid-gap $grid-gap 0;\n  \n      @each $min-width, $width in $grid-map {\n        @media screen and (min-width: $min-width) {\n          width: $width;\n        }\n      }\n    }\n  }\n}\n\n@mixin settings-header-text() {\n  font-weight: normal;\n  font-size: 24px;\n  color: $text-dark-grey;\n}\n\n@mixin pulsing-highlight {\n  @include keyframes(pulse) {\n    0% {\n      @include box-shadow(0 0 0 0 $blue-opaque-0)\n    }\n    70% {\n      @include box-shadow(0 0 1px 4px $blue-opaque-2)\n    }\n    100% {\n      @include box-shadow(0 0 0 0 $blue-opaque-0)\n    }\n  }\n  @include animation(pulse 2s infinite);\n}\n\n@mixin zebra($oddColor, $evenColor) {\n  > :nth-child(2n + 1) { background: $oddColor; }\n  > :nth-child(2n)     { background: $evenColor; }\n}\n\n@mixin popperTooltip($bg: $text-dark-grey, $color: $white, $border-color: $white, $font-size: 10px, $arrow-size: 8px) {\n  [data-popper-arrow],\n  [data-popper-arrow]::before,\n  [data-popper-arrow]::after {\n      position: absolute;\n      width: $arrow-size;\n      height: $arrow-size;\n  }\n\n  [data-popper-arrow] {\n      visibility: hidden;\n  }\n\n  [data-popper-arrow]::before, [data-popper-arrow]::after {\n      visibility: visible;\n      content: '';\n      // arrow is actually a rotated square, because that's easier\n      transform: rotate(45deg);\n      left: 0;\n  }\n  \n  [data-popper-arrow]::before {\n      background: $border-color;\n  }\n  [data-popper-arrow]::after {\n      background: $bg;\n  }\n\n  $arrow-offset: math.div(-$arrow-size, 2);\n\n  &[data-popper-placement^='top'] > [data-popper-arrow] {\n      bottom: $arrow-offset;\n      &::after {\n          margin-top: -1px;\n      }\n  }\n\n  &[data-popper-placement^='bottom'] > [data-popper-arrow] {\n      top: $arrow-offset;\n      &::after {\n          margin-top: 1px;\n      }\n  }\n\n  &[data-popper-placement^='left'] > [data-popper-arrow] {\n      right: $arrow-offset;\n      &::after {\n          margin-left: -1px;\n      }\n  }\n\n  &[data-popper-placement^='right'] > [data-popper-arrow] {\n      left: $arrow-offset;\n      &::after {\n          margin-left: 1px;\n      }\n  }\n  // give our tooltip maximum z-index possible to make sure it doesn't covered by anything near pin\n  z-index: 2147483647 !important;\n  padding: 11px 13px !important;\n  border-radius: 5px !important;\n  border: 1px solid $border-color !important;\n  background: $bg !important;\n  color: $color !important;\n  font-size: $font-size !important;\n  font-weight: $font-weight-medium !important;\n  font-family: $font-family !important;\n  line-height: 1.3;\n}\n\n@mixin zendesk-badge() {\n  width: 16px;\n  height: 16px;\n  background: $red-5;\n  position: absolute;\n  border-radius: 10px;\n  color: $white;\n  padding: 1px;\n  text-align: center;\n  font-size: 12px;\n  visibility: hidden;\n}\n\n@mixin scrollable-text() {\n  overflow-x: scroll;\n  @include no-scrollbar();\n  white-space: nowrap;\n}\n","@import \"~@ceros/markup-common/frontend/static/theme.scss\";\n@import \"~@ceros/markup-common/frontend/static/mixin.scss\";\n\nul li.create-project {\n  .content-column {\n    .failed {\n      color: $dark-red;\n    }\n  }\n  .plus {\n    font-size: 18px;\n    width: 18px;\n    line-height: 18px;\n    text-align: center;\n    color: $blue;\n    border: 1px solid $blue;\n    border-radius: 50%;\n  }\n  .error {\n    @include round-x(20px, 17px);\n    color: $white;\n    background-color: $dark-red;\n  }\n  .loading-spinner {\n    width: 20px;\n    height: 20px;\n    @include html-spinner(20px);\n  }\n  &:hover .plus {\n    color: $white;\n    background: $blue;\n  }\n\n  &.disabled, &.disabled:hover {\n    .plus {\n      color: $grey;\n      border-color: $grey;\n      background: initial;\n    }\n  }\n}\n","$black: #000000;\n$black-opaque-5: rgba(0, 0, 0, 0.5);\n$black-opaque-7: rgba(0, 0, 0, 0.7);\n$blue: #1b00fb;\n$blue-opaque-0: rgba(27, 0, 25