s;\n  -ms-animation: $args;\n  -o-animation: $args;\n  animation: $args;\n}\n\n@mixin box-shadow($args...) {\n  -webkit-box-shadow: $args;\n  -moz-box-shadow: $args;\n  box-shadow: $args;\n}\n\n@mixin border-radius($args...) {\n  -webkit-border-radius: $args;\n  -moz-border-radius: $args;\n  border-radius: $args;\n}\n\n@mixin filter($args...) {\n  -ms-filter: $args;\n  -webkit-filter: $args;\n  filter: $args;\n}\n\n@mixin arrow-piece-horizontal($size, $left) {\n  content: '';\n  position: absolute;\n  left: $left;\n  width: 0;\n  height: 0;\n  border: $size solid transparent;\n  margin-left: -$size;\n}\n\n@mixin arrow-top($left: 50%, $size: 10px, $border-color: $light-grey-2, $bg-color: $white) {\n  &:before {\n    @include arrow-piece-horizontal($size, $left);\n    top: 1px;\n    border-top: 0;\n    border-bottom-color: $border-color;\n    margin-top: -$size - 1;\n  }\n  &:after {\n    @include arrow-piece-horizontal($size, $left);\n    top: 1px;\n    border-top: 0;\n    border-bottom-color: $bg-color;\n    margin-top: -$size;\n  }\n}\n\n@mixin arrow-bottom($left: 50%, $size: 10px, $border-color: $light-grey-2, $bg-color: $white) {\n  &:before {\n    @include arrow-piece-horizontal($size + 3, $left);\n    bottom: -1px;\n    border-bottom: 0;\n    border-top-color: $border-color;\n    margin-left: -$size - 2;\n    margin-bottom: -$size - 1;\n  }\n  &:after {\n    @include arrow-piece-horizontal($size, $left);\n    bottom: 1px;\n    border-bottom: 0;\n    border-top-color: $bg-color;\n    margin-bottom: -$size;\n  }\n}\n\n@mixin arrow-right($size, $top, $bg-color) {\n  &:after {\n    @include arrow-piece-horizontal($size, 100%);\n    top: $top;\n    border-left-color: $bg-color;\n    border-right: 0;\n    margin-top: -$size;\n  }\n}\n\n@mixin arrow-right-bordered($top: 0px, $size: 10px, $border-color: $light-grey-2, $bg-color: $white) {\n  &:before {\n    @include arrow-piece-horizontal($size + 1, 100%);\n    top: $top;\n    border-left-color: $border-color;\n    border-right: 0;\n    margin-left: unset;\n    margin-top: -$size;\n    margin-right: -$size - 2;\n  }\n  &:after {\n    @include arrow-piece-horizontal($size, -2px);\n    top: calc($top + 1px);\n    border-left-color: $bg-color;\n    border-right: 0;\n    margin-left: unset;\n    margin-top: -$size;\n    margin-right: -$size;\n  }\n}\n\n@mixin arrow-left($size, $top, $bg-color) {\n  &:after {\n    @include arrow-piece-horizontal($size, 0);\n    top: $top;\n    border-right-color: $bg-color;\n    border-left: 0;\n    margin-top: -$size;\n  }\n}\n\n@mixin arrow-left-bordered($top: 0px, $size: 10px, $border-color: $light-grey-2, $bg-color: $white) {\n  &:before {\n    @include arrow-piece-horizontal($size + 1, 100%);\n    top: $top;\n    border-right-color: $border-color;\n    border-left: 0;\n    margin-top: -$size;\n    margin-left: -$size - 1;\n  }\n\n  &:after {\n    @include arrow-piece-horizontal($size, 1px);\n    top: calc($top + 1px);\n    border-right-color: $bg-color;\n    border-left: 0;\n    margin-top: -$size;\n    margin-right: -$size;\n  }\n}\n\n// this is taken from https://loading.io/spinner\n@mixin html-spinner($side-length: 80px, $color: #000) {\n  position: relative;\n  div {\n    box-sizing: border-box;\n    display: block;\n    position: absolute;\n    width: $side-length;\n    height: $side-length;\n    border: math.div($side-length, 16) solid $color;\n    border-radius: 50%;\n    animation: html-spinner 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;\n    border-color: $color transparent transparent transparent;\n\n    &:nth-child(1) {\n      animation-delay: -0.45s;\n    }\n    &:nth-child(2) {\n      animation-delay: -0.3s;\n    }\n    &:nth-child(3) {\n      animation-delay: -0.15s;\n    }\n  }\n}\n@keyframes html-spinner {\n  0% {\n    transform: rotate(0deg);\n  }\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n@mixin html-spinner-overlay($side-length: 80px, $opacity: 1, $color: #000) {\n  position: absolute;\n  top: 0;\n  bottom: 0;\n  left