import * as React from "react";
import {IExtensionUserInfo} from "@ceros/markup-common/frontend/extension/extension-user-info";
import "./clear-cookies.scss";
import {getActiveTab, sendMessage} from "../../shared/chrome-api-promises";
import {APP_ORIGIN} from "../../shared/app-origin";
import ClearIcon from "../assets/delete-icon-outline.svg.inline";
import {fireAndForget} from "@ceros/markup-common/shared/util/promise-utils";
import {MessageToExtensionBackgroundScriptType} from "@ceros/markup-common/frontend/extension/messages/extension-background-script-message";
import {Logger} from "@ceros/markup-common/frontend/logger";
import {isUUID} from "@ceros/markup-common/shared/util/string-utils";

interface IProps {
    userInfo: IExtensionUserInfo;
    projectId: string;
}

interface IState {
    isRefreshing: boolean;
    didError: boolean;
}

export class ClearCookies extends React.Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);

        this.state = {
            isRefreshing: false,
            didError: false
        };
    }

    public render(): JSX.Element | null {
        return (
            <li className={`create-project ${this.state.isRefreshing ? "disabled" : ""}`} onClick={this.onClick.bind(this)}>
                <div className="image-column">{this.renderIcon()}</div>
                <div className="content-column">
                    <span>Clear cookies</span>
                    {this.state.didError && <span className="failed">Failed</span>}
                </div>
            </li>
        );
    }

    private renderIcon(): JSX.Element {
        if (this.state.isRefreshing) {
            return (
                <div className="loading-spinner">
                    <div></div>
                    <div></div>
                    <div></div>
                    <div></div>
                </div>
            );
        }
        return <span className="refresh" dangerouslySetInnerHTML={{__html: ClearIcon}} />;
    }

    private onClick(): void {
        if (this.state.isRefreshing) {
            return;
        }
        this.setState({isRefreshing: true, didError: false});
        fireAndForget(
            (async (): Promise<void> => {
                const messageAccepted: boolean = await sendMessage({
                    type: MessageToExtensionBackgroundScriptType.POPUP_CLEAR_COOKIES,
                    projectId: this.props.projectId
                });
                if (messageAccepted) {
                    this.setState({isRefreshing: false});
                } else {
                    this.setState({isRefreshing: false, didError: true});
                }
            })(),
            Logger
        );
    }
}
